* {
    margin:0;
    padding: 0;
}

html, body {
    overflow: hidden;
}

canvas{
    background-image: radial-gradient(circle, rgb(242, 203, 171), rgb(242, 134, 0));
}

.webgl {
    position: fixed;
    top:0;
    left: 0;
    outline: none;
    
}

.loading-bar {
    position: absolute;
    top: 50%;
    width: 100%;
    height:2px;
    background: white;
    transform: scaleX(0);
    transition: transform 0.5s;
    will-change: transform;
}

.loading-bar.ended {
    transition: transform 1.5s ease-in-out;
}

.loading-text{
    color: white;
}
